.card-group {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  margin: 50px 50px;
  grid-gap: 20px;
}
.artisancard {
  display: grid;
  border: 1px solid gray;
  grid-template-rows: 200px 100px 50px;
  margin: 0px;
  padding: 0px;
  box-shadow: inset 0 0 16px black;
  -moz-box-shadow: inset 0 0 16px black;
  -webkit-box-shadow: inset 0 0 16px black;
}

.artisancard .text {
  display: grid;
  grid-template-rows: 1fr 1fr;
}

.imgbanner {
  display: grid;
  margin: auto;
}

.imgbanner img {
  height: 200px;
  max-width: 100%;
}

.username {
  margin: 10px 0px;
}

.username p {
  text-align: center;
}

.username h3 {
  font-size: 16px;
  text-align: center;
}

.text .profile {
  grid-template-columns: 1fr 2fr 1fr;
  display: grid;
  grid-gap: 0px;
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
  background: whitesmoke;
}

.imgprofile img {
  border: 1px solid gray;
  margin: 5px 10px;
  width: 80%;
  border-radius: 50%;
  height: 70px;
  background: whitesmoke;
}
.user-description p {
  text-align: center;
  margin: 10px 0px;
  width: 80%;
  padding: 10px 0px;
  margin: auto auto;
}

.hireme {
  margin: auto;
}

.hireme button {
  color: white;
  background: blue;
  border-radius: 10px;
  padding: 5px;
  border: 1px solid blue;
}

.hireme button:hover {
  background: red;
  color: black;
}

.user-description .status {
  border-top-right-radius: 10px;
  border: 1px solid gray;
  padding: 2px;
  box-shadow: 5px 5px 10px 2px gray;
  border-top-left-radius: 10px;
  margin-top: 1px;
  background: blue;
  color: white;
  width: 50%;
}

p.description {
  margin: -15px 0px 15px 30px;
}

.user-description p.status {
  margin-top: -22px;
}

@media (max-width: 500px) {
  .card-group {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    margin: 40px 10px;
  }

  .artisancard {
    grid-template-rows: 100px 70px 70px;
    /* padding-bottom: 80px; */
    margin-bottom: 25px;
  }

  .imgbanner img {
    height: 100px;
    max-width: 150px;
  }

  .username h3 {
    font-size: 0.8rem !important;
  }

  .text .profile {
    grid-template-columns: 60px 1fr;
    height: 70px;
  }
}

@media (min-width: 320px) and (max-width: 450px) {
  .user-description p.description {
    margin: -5px 10px 10px 15px;
  }
  .hireme button {
    position: absolute;
    margin-top: -190px;
    border-right: 0px;
    margin-left: 20px;
  }
  .username p {
    font-size: 0.75rem;
    text-align: center;
  }

  .imgprofile img {
    width: 40px;
    height: 60px;
  }

  .user-description .status {
    padding: 2px;
    margin-top: -12px;
  }
}

@media (width: 411px) {
  .hireme button {
    margin-left: 38px;
    border-radius: 0px;
  }
}

@media (max-width: 380px) {
  .user-description .status {
    padding: 2px;
    margin-top: -12px;
  }
}

@media (min-width: 385px) and (max-width: 430px) {
  .user-description .status {
    padding: 2px;
    margin-top: -10px;
  }
}

@media (width: 320px) {
  .hireme button {
    margin-left: 10px;
  }
}
